import React, { Component } from 'react';
import Add from './add'
import Subtract from './subtract'
import InputOwn from './input'
import Reset from './reset'
import Mult from './mult'

class App extends Component {
  state = {
      count: 0
    };
    
    add = () => {
      this.setState({
        count: this.state.count + 1
      });
    }
  
    subtract = () => {
      this.setState({
        count: this.state.count - 1
      });
    }
  
    inputOwn = (number) => {
      let value = parseInt(number);
      this.setState({
        count: this.state.count + value
      });
    }

    mult = (number) => {
      let value = parseInt(number);
      this.setState({
        count: this.state.count * value
      });
    }

    reset = () => {
      this.setState({
        count: 0
      })
    }
    
  
    render() {
      return (
        <div className="container">
        <div className="counter">
          <h1>{this.state.count}</h1></div>
          <br/>
          <div className="updown">
          <Add add={this.add} />
          <br/>
          <Subtract subtract={this.subtract} /></div>
          <br/>
          <div className="input">
          <InputOwn inputOwn={this.inputOwn} /></div>
          <br/>
          
          <div className="reset">
          <Reset reset={this.reset} /></div>
          <div className="mult">
          <Mult mult={this.mult} /></div>
        </div>
      );
    }
  }

export default App;
