import React, { Component } from 'react';

class Mult extends Component {
    state = {
        count: ""
    }

    handleChange = (e) => {
        this.setState({
            count: e.target.value
        })
    }

    handleSubmit = (e) => {
        e.preventDefault();
        this.props.mult(this.state.count);
        this.setState({
           count: ""
       });
      }

    render() {
        return (
        <div className="form-container">
            <form onSubmit={this.handleSubmit}>
                <input className="multbox" onChange={this.handleChange} value={this.state.count} type="text" placeholder="Hmm..." />
                <br/><br/>
                <button className="multbtn">Go Crazy!</button>
            </form>
        </div>
        )
    }
}

export default Mult